@import url(//db.onlinewebfonts.com/c/229a57bbeef591e04a1fdfe347c90864?family=Centra+No2);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {font-family: "Centra No2"; src: url("//db.onlinewebfonts.com/t/229a57bbeef591e04a1fdfe347c90864.eot"); src: url("//db.onlinewebfonts.com/t/229a57bbeef591e04a1fdfe347c90864.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/229a57bbeef591e04a1fdfe347c90864.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/229a57bbeef591e04a1fdfe347c90864.woff") format("woff"), url("//db.onlinewebfonts.com/t/229a57bbeef591e04a1fdfe347c90864.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/229a57bbeef591e04a1fdfe347c90864.svg#Centra No2") format("svg"); }

body{
  background-color: #191D32;
  color:#FF3366;
  font-family: 'Centra No2';
}

main{
  width: 38rem;
  margin: 50px auto 0;
}

div.board{
  display: grid;
  border: 5px solid #fff;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

div.square{
  width: 200px;
  height: 200px;
  border: 5px solid #fff;
  box-sizing: border-box;
  font-size: 7rem;
  text-align: center;
  padding: 30px;
}

@media(max-width:40rem){
  main{
    width: 308px;
    margin: 250px auto 0;
  }
  
  div.board{
    display: grid;
    border: 4px solid #fff;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  
  div.square{
    width: 100px;
    height: 100px;
    border: 4px solid #fff;
    box-sizing: border-box;
    font-size: 4rem;
    text-align: center;
    padding: 5px;
  }
}

div.result{
  text-align: center;
  font-size: 2rem;
  padding: 0.6rem;
}

div.result.green{
  background-color: #F6F7F8;
}

div.result.red{
  background-color: #F6F7F8;
}
p{
  font-size: .7rem;
  padding: 0;
  margin: 0;
}
.flexDiv{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
}
button {
  font-weight: 700;
  color: #fff;
  border: 1px solid #FF3366;
  padding: 18px 34px;
  font-size: 18px;
  margin-left: 18px;
	position: relative;
	background-color: transparent;
	transition: 0.3s ease-in-out;
  border-radius: 5%;
  font-family: 'Centra No2';
}

button::before {
  content: "";
  width: 0%;
  height: 100%;
  position: absolute;
  background-color: #FF3366;
  left: 0;
  top: 0;
  z-index: -1;
  transition: 0.3s ease-in-out;
}
button:hover {
  color: #191D32;
}
button:hover::before {
	content: "";
	width: 100%;
	height: 100%;
	position: absolute;
}
